import { Component } from '@angular/core';

@Component({
  selector: 'frame',
  templateUrl: 'frame.html'
})
export class FrameComponent {

  text: string;

  constructor() {
    console.log('Hello FrameComponent Component');
    this.text = 'Hello World';
  }

}



